
import { Category } from "@/interface";
import { defineComponent } from "vue";
import { useRoute } from "vue-router";
import ActionButtonComponent from "../components/ActionButtonComponent.vue";
import ItemComponent from "../components/ItemComponent.vue";

export default defineComponent({
  props: ["categories"],
  emits: ["showItemDetail"],
  components: {
    ActionButtonComponent,
    ItemComponent,
  },
  computed: {
    category(): Category {
      const categoryName = useRoute().params.categoryName;
      const category = this.categories.find(
        (c: Category) => c.name === categoryName
      );
      if (category) {
        window.document.title = `${category.name} - Menu Panda`;
      }
      return category ? category : [];
    },
  },
  methods: {
    showItemDetail(code: string) {
      this.$emit("showItemDetail", code);
    },
  },
});
